        busCode: "", //业务代码
        busName: "", //业务名称
        dpname: "", //所属业务部门
        createDate: "", //业务创建时间
        busDescribe: "", //业务描述
        abroad: "", //境内外客户
        investmentMark: "", //投资境外
        riskLevel: "" //洗钱分线等级
      },
      busRiskEval: {
        //业务信息2
        totalScore: "", //综合评分
        sysLevel: "", //系统评估等级
        confirmLevel: "", //洗钱风险按等级
        updater: "", //评估人员
        updateTime: "", //评估日期
        nextEvalDate: "", //下次评估日期
        remark: "", //评估备注
        fileName: "", //评估附件
        confirmLevel: "" //确认等级
      },
      adjustmentData: [
        //调整列表
        {
          adjustLevel: "", //调整等级
          updateTime: "", //调整日期
          updater: "", //调整人员
          fileName: "", //附件
          filePath: "", //附件
          nextEvalDate: "", //下次评估日期
          causeDetail: "" //事由说明
        }
      ],
      historyDetail: [
        {
          department: "", //部门名称
          type: "", //评估类型
          totalScore: "", //综合评分
          sysLevel: "", //系统评估等级
          evalUpdateTime: "", //评估日期
          evalUpdater: "", //评估人员
          riskLevel: "", //洗钱风险等级
          evalRemark: "", //评估备注
          evalFileName: [], //评估附件
          evalFilePath: [], //评估附件
          adjStatus: "", //审核状态
          evalStatus: "", //审核状态
          // type为1的时候用evalStatus,type为2的时候用adjStatus
          adjustLevel: "", //调整等级
          adjTime: "", //调整日期
          adjCreator: "", //调整人员
          causeDetail: "", //调整事由说明
          adjFileName: "" //调整附件
        }
      ],
      modelTitle: "流程详情",
      thead: [
        { name: "部门" },
        { name: "评估类型" },
        { name: "综合评分" },
        { name: "系统评估等级" },
        { name: "评估日期" },
        { name: "评估人员" },
        { name: "洗钱风险等级" },
        { name: "评估备注" },
        { name: "评估附件" },
        { name: "审核状态" },
        { name: "调整等级" },
        { name: "调整日期" },
        { name: "调整人员" },
        { name: "调整事由说明" },
        { name: "调整附件" },
        { name: "流程详情" }
      ],
      tbody: [
        { name: "部门" },
        { name: "评估日期" },
        { name: "评估人员" },
        { name: "洗钱风险等级" },
        { name: "综合评分" },
        { name: "系统评估等级" },
        { name: "评估附件" },
        { name: "调整等级" },
        { name: "调整时间" },
        { name: "调整人员" },
        { name: "调整事由" },
        { name: "调整事由说明" }
      ]
    };
  },
  components: {
    processMessage
  },
  mounted(){
    console.log('mounted');
    this.$nextTick(() => {
      window.addEventListener('scroll', this.changeScrol, true); 
    })
  },
  destroyed() {
    console.log('destroyed');
    //离开该页面需要移除这个监听的事件
    window.removeEventListener("scroll", this.changeScrol,true);
  },
  methods: {
    changeScrol() {
      /**
       * getBoundingClientRect().top 获取某元素距离浏览器顶部的高度，不包含滚动的距离
        this.offsetTop 表示的是吸顶元素距离顶部的条件值（一般项目需求是0）
       */
      let targetOffsetTop = this.$refs.target.getBoundingClientRect().top;
      let historyOffsetTop = this.$refs.history.getBoundingClientRect().top;
      console.log('targetOffsetTop', targetOffsetTop);
      console.log('historyOffsetTop', historyOffsetTop);
      if(targetOffsetTop < 0){
        this.positonFix = true;
        if(historyOffsetTop < 20){
          this.positonFix = false;
        }
      }else{
        this.positonFix = false;
      }
    },
    activeButtoFun(a) {
      let that = this;
      let data = {
        busId: this.dataObjj.busId,
        newBusId: this.dataObjj.newBusId,
        type: this.dataObjj.type,
        evalBusinessId: this.dataObjj.evalBusinessId,
        adjBusinessId: this.dataObjj.adjBusinessId,
        level: a
      };
      data = JSON.stringify(data);
      this.axios
        .post("/risk/busi/getDetailByLevel", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          that.activeNames = response.data;
        })
        .catch(error => {});
    },
    watchFiles(id) {
      if(id){
        this.axios
        .post(`/system/remote/getFileList/${id}`)
        .then(res => {
          if (res.code == "200") {
            this.fileList = res.data;
          }
        })
        .catch(function(error) {});
      }
      this.modal1 = true;
      
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    handleChange() {},
    showProcess(item, name) {
      if (name == "history") {
        if (item.type == "评估") {
          this.dataObj = {
            businessId: item.evalBusinessId,
            name: "业务洗钱风险评估"
          };
          this.businessId = item.evalBusinessId;
        } else {
          this.dataObj = {
            businessId: item.adjBusinessId,
            name: "业务洗钱风险评估"
          };
          this.businessId = item.adjBusinessId;
        }
      } else {
        this.businessId = item.businessId;
        this.dataObj = {
          businessId: item.businessId,
          name: "业务洗钱风险评估"
        };
      }
      this.tabview = "processMessage";
      this.modelTitle = "流程信息";
      this.showModal = true;
    },
    cancel() {}
  }
};
</script>
